import { useEffect } from 'react';

export const LoadTest = ({ setTest, idTopic, idTest }) => {
  useEffect(() => {
    const fetchTest = async () => {
      try {
        const module = await import(`../tests/tests`); //  JSON
        const testsData = module.default.testsfile; //   

        if (testsData.length > 0 && testsData[0].tests.length > 0) {
          const test = testsData[idTopic - 1].tests[idTest - 1];
          setTest(test);
          console.log("Test:", test);
        } else {
          console.warn("  .");
        }
      } catch (error) {
        console.error('   :', error);
      }
    };

    fetchTest();
  }, [idTopic, idTest, setTest]);

  return null;
};