import { useEffect, useState } from 'react';

export const LoadTestName = ({ setName, id }) => {
  useEffect(() => {
    setName([]); //   

    const fetchTest = async () => {
      try {
        const module = await import(`../tests/tests`); //  JSON
        const testsData = module.default.testsfile; //   

        if (testsData.length > 0 && testsData[0].tests.length > 0) {
          const names = testsData[id - 1].tests.map(item => item.name);
          console.log("  :", names);
          setName([names]); 
        } else {
          console.warn("  .");
          setName([]);
        }
      } catch (error) {
        console.error('   :', error);
        setName([]);
      }
    };

    fetchTest();
  }, [setName]); 

  return null;
};
